.header .header_wrapper {
  display: flex;
  justify-content: space-between;
  height: 38px;
  width: 280px;
  outline: 1px solid #3700ff;
  outline-offset: -1px;
}
@media (min-width: 748px) {
  .header .header_wrapper {
    flex-direction: row;
    height: 52px;
    width: 174px;
  }
}
@media (min-width: 1260px) {
  .header .header_wrapper {
    flex-direction: row;
    height: 59px;
    width: 202px;
  }
}
.main-nav {
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  font-family: "Oswald";
  text-transform: uppercase;
}
.main-nav .main-nav_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 198px;
  outline: 1px solid red;
  outline-offset: -1px;
}
.main-nav .main-nav_wrapper .site-list {
  padding: 0;
  margin: 0 auto;
}
@media (min-width: 748px) {
  .main-nav .main-nav_wrapper {
    flex-direction: row;
    justify-content: space-between;
    height: 36px;
    width: 440px;
  }
}
@media (min-width: 1260px) {
  .main-nav .main-nav_wrapper {
    flex-direction: row;
    justify-content: space-between;
    height: 40px;
    width: 478px;
  }
}
